@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body, h1, h2, h3, h4, h5, h6, *, p {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
}

html, body {
  height: 100%;
  
}

.App {
  background: #2a6caa;
  background: linear-gradient(#448acc, #205586, #0c1c2a);
  padding-bottom: 40px;
}

/* Header CSS */
header {
  color: #FFF;
  padding: 20px 10px;
  border-bottom: 2px solid #3d84c7;
}

.header-creator {
  width: 100%;
  max-width: 700px;
  margin: 0 auto;
}

.header-creator p {
  padding: 5px 0;
  font-size: 18px;
  text-align: right;
}


/* Main Content CSS */

.main-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 0 auto;
  max-width: 750px;
}

.main-weather-content-wrapper {
  width: 100%;
  padding: 15px;
  color: #FFF;
}

.pre-load-content {
  width: 100%;
  height: 70vh;
  padding: 20px 10px;
  text-align: center;
}

.load-weather-text {
  color: #FFF;
  font-size: 30px;
  margin-bottom: 30px;
  font-weight: 400;
}

.input-field {
  font-size: 20px;
  box-sizing: border-box;
  border-radius: 4px;
  min-width: 220px;
  padding: 10px 5px;
  border: 0;
}


.error-text {
  text-align: center;
  font-size: 18px;
  color: #c33f3f;
  background: #0c1c2a;
  padding: 30px 10px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100000;
}

.weather-details-wrapper {
  background: #27639b;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding: 15px 15px;
  border-radius: 4px;
}


.weather-details-wrapper p {
  font-size: 18px;
  margin-bottom: 15px;
}

.weather-for {
  font-size: 40px;
  margin-bottom: 20px;
  border-bottom: 1px solid #FFF;
  padding-bottom: 10px;
}


.daily-weather-wrapper {
  width: 100%;
  max-width: 1000px;
  margin: 10px 0 35px 0;
}

.daily-weather-list {
  list-style-type: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.daily-weather-list .each-day {
  padding: 10px;
  margin: 10px;
  width: 120px;
  border-radius: 4px;
  text-align: center;
}

.daily-weather-list .today {
  width: 95%;
  padding: 15px 0px 15px 0px;
  margin-bottom: 10px;
  text-align: center;
}

.today .daily-day-text {
  font-weight: bold;
  font-size: 32px;
  margin-bottom: 5px;
}

.each-day .daily-day-text {
  font-size: 22px;
  margin-bottom: 3px;
}

.today > p {
  font-size: 18px;
}

.today .daily-icon img {
  width: 120px;
  height: 120px;
}

.daily-icon img {
  width: 80px;
  height: 80px;
}

.daily-weather-text {
  margin-bottom: 10px;
}


.hour-wrapper {
  width: 100%;
  height: 130px;
  list-style-type: none;
  border-radius: 4px;
  border: 1px solid #FFF;
  margin-top: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  overflow-x: auto;
  box-shadow: inset -15px 0 9px -7px rgba(0,0,0,0.4);
}

.hour-wrapper li {
  min-width: 75px;
  text-align: center;
}

.hour-number {
  font-size: 16px;
}

.hourly-icon img {
  width: 50px;
  height: 50px;
}



/* Menu CSS */

.new-location-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(#2c6192, #12314e, #0c1c2a);
  text-align: center;
  color: #FFF;
  z-index: 10000;
}


.new-location-menu {
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.new-location-menu p {
  font-size: 24px;
  padding-bottom: 10px;
  margin-bottom: 15px;
}


.close-new-location-menu, .open-new-location-menu {
  color: #FFF;
  font-size: 30px;
  cursor: pointer;
}

.close-new-location-menu {
  position: absolute;
  right: 20px;
  top: 20px;
}

.open-new-location-menu {
  position: absolute;
  left: 0px;
  top: 25px;
  padding: 10px;
  width: 50px;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
  text-align: center;
  background: #28639b;
  opacity: 0.95;
}


/* Footer & Misc CSS */

footer {
  width: 100%;
  padding: 8px 10px 20px 10px;
  color: #FFF;
  position: fixed;
  bottom: 0;
  background-color: #193a57;
}

footer p {
  font-size: 14px;
  text-align: center;
  margin: 10px 0;
}

footer .copyright-text {
  font-size: 12px;
}


.hidden {
  display: none;
  transition: all 300ms ease-in;
}


.transition-enter {
  opacity: 0;
  -webkit-transform: translate(0, -10px);
          transform: translate(0, -10px);
}

.transition-enter-active {
  opacity: 1;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  transition: all 300ms ease-in;
}

.transition-exit {
  opacity: 1;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}

.transition-exit-active {
  opacity: 0;
  -webkit-transform: translate(0, -10px);
          transform: translate(0, -10px);
  transition: all 300ms ease-in;
}



.weatherTransition-enter {
  opacity: 0;
  -webkit-transform: translate(-50px, 0);
          transform: translate(-50px, 0);
}

.weatherTransition-enter-active {
  opacity: 1;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  transition: all 750ms ease-in;
}

.weatherTransition-exit {
  opacity: 1;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}

.weatherTransition-exit-active {
  opacity: 0;
  -webkit-transform: translate(50px, 0);
          transform: translate(50px, 0);
  transition: all 750ms ease-in;
}




.menuTransition-enter {
  opacity: 0;
}

.menuTransition-enter-active {
  opacity: 1;
  transition: all 400ms ease-in;
}

.menuTransition-exit {
  opacity: 1;
  transition: all 400ms ease-out;
}

.menuTransition-exit-done {
  opacity: 0;
  transition: all 400ms ease-out;
}



@media screen and (min-width: 768px ) {
  .main-wrapper {
    margin: 40px auto;
  }
  .header-creator p {
    font-size: 22px;
  }
  .load-weather-text {
    font-size: 42px;
  }
  .new-location-wrapper {
    padding: 35px;
  }
  .daily-weather-list .each-day {
    width: 220px;
  }
  .daily-icon img {
    width: 100px;
    height: 100px;
  }

  .input-field {
    font-size: 26px;
  }
  .new-location-menu p {
    font-size: 36px;
    margin-bottom: 25px;
  }

}
